import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import Navbar from "../components/Navbar";

const SplitDetails = () => {
  const location = useLocation();
  const transaction = location.state;

  const [paidStatus, setPaidStatus] = useState({});

  useEffect(() => {
    if (transaction) {
      const { hostUpiId, participants, amount } = transaction;
      const splitAmount = (amount / participants.length).toFixed(2);
      const updatedStatus = {};

      participants.forEach((p) => {
        const transactionId = `${p}-${splitAmount}-${hostUpiId}`;
        updatedStatus[p] = localStorage.getItem(transactionId) === "paid";
      });

      setPaidStatus(updatedStatus);
    }
  }, [transaction]);

  if (!transaction) {
    return <p className="error">‚ùå No transaction found.</p>;
  }

  const { title, amount, hostUpiId, participants } = transaction;
  const splitAmount = (amount / participants.length).toFixed(2);

  // ‚úÖ Dynamically get the full base URL
  const repoName = "splitzy"; // Change to your actual repo name
  const baseURL = `${window.location.origin}/${repoName}/#/pay`;

  const copyToClipboard = async (paymentLink) => {
    try {
      await navigator.clipboard.writeText(paymentLink);
      alert("‚úÖ Payment link copied! Share it with the participant.");
    } catch (err) {
      console.error("‚ùå Copy failed:", err);
      alert("‚ùå Failed to copy link. Try manually selecting and copying.");
    }
  };

  return (
    <div className="container">
      <Navbar />

      <div className="header">
        <h2>{title}</h2>
        <p className="sub-text">Total Amount: ‚Çπ{amount}</p>
      </div>

      <div className="card upi-card">
        <p className="label">Host UPI ID</p>
        <p className="upi">{hostUpiId}</p>
      </div>

      <h3 className="sub-heading">Participants</h3>
      <ul className="participants-list">
        {participants.map((p, index) => {
          // ‚úÖ Ensure the full URL is correctly included
          const paymentPageURL = `${baseURL}?name=${encodeURIComponent(p)}&amount=${splitAmount}&upi=${encodeURIComponent(hostUpiId)}`;
          console.log(`Generated Payment Link for ${p}:`, paymentPageURL); // Debugging

          return (
            <li key={index} className="card participant">
              <div>
                <p className="participant-name">{p}</p>
                <p className="split-amount">‚Çπ{splitAmount}</p>
              </div>
              {paidStatus[p] ? (
                <p className="success-message">‚úÖ Paid</p>
              ) : (
                <button className="button small" onClick={() => copyToClipboard(paymentPageURL)}>
                  Copy Link
                </button>
              )}
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default SplitDetails;
