import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";

const PaymentPage = () => {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);

  const participantName = queryParams.get("name") || "Guest";
  const amount = queryParams.get("amount") || "0";
  const hostUpiId = queryParams.get("upi") || "";
  const transactionId = `${participantName}-${amount}-${hostUpiId}`;
  const upiLink = `upi://pay?pa=${hostUpiId}&pn=${encodeURIComponent(
    participantName
  )}&am=${amount}&cu=INR`;

  const [paymentSuccess, setPaymentSuccess] = useState(
    localStorage.getItem(transactionId) === "paid"
  );
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    const handleVisibilityChange = () => {
      if (document.visibilityState === "visible" && !paymentSuccess) {
        setShowModal(true);
      }
    };

    document.addEventListener("visibilitychange", handleVisibilityChange);
    return () =>
      document.removeEventListener("visibilitychange", handleVisibilityChange);
  }, [paymentSuccess]);

  const handleConfirmPayment = () => {
    localStorage.setItem(transactionId, "paid");
    setPaymentSuccess(true);
    setShowModal(false);
  };

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col justify-between">
      {/* Header */}
      <div className="bg-black text-white p-5 rounded-b-3xl shadow-md">
        <h2 className="text-2xl font-semibold">Deposit</h2>
      </div>

      {/* Payment Card */}
      <div className="flex flex-col items-center justify-center p-6">
        <div className="bg-white rounded-3xl shadow-lg p-6 w-11/12 text-center">
          <h3 className="text-gray-700 text-xl font-semibold">Bank SCA</h3>
          <p className="text-gray-500 mt-1">Deposit on {new Date().toDateString()}</p>
          <h1 className="text-5xl font-bold text-black my-5">‚Çπ{amount}</h1>

          {paymentSuccess ? (
            <p className="text-green-600 text-lg font-bold">‚úÖ Payment Successful!</p>
          ) : (
            <button
              onClick={() => (window.location.href = upiLink)}
              className="w-full bg-black text-white py-3 rounded-full mt-4 text-lg font-medium"
            >
              Pay Now
            </button>
          )}
        </div>
      </div>

      {/* Confirmation Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center">
          <div className="bg-white p-6 rounded-xl shadow-lg w-80 text-center">
            <h3 className="text-xl font-semibold">Confirm Payment</h3>
            <p className="text-gray-600 my-3">Did you complete the payment?</p>
            <button
              onClick={handleConfirmPayment}
              className="w-full bg-green-500 text-white py-2 rounded-md text-lg font-medium mb-2"
            >
              Yes, I Paid
            </button>
            <button
              onClick={() => setShowModal(false)}
              className="w-full bg-gray-300 py-2 rounded-md text-lg font-medium"
            >
              Cancel
            </button>
          </div>
        </div>
      )}

      {/* Bottom Navigation */}
      <div className="bg-white shadow-lg py-3 flex justify-around items-center fixed bottom-0 w-full rounded-t-3xl">
        <button className="text-gray-500 text-2xl">üè†</button>
        <button className="bg-black text-white text-2xl rounded-full p-3">üí≤</button>
        <button className="text-gray-500 text-2xl">üìú</button>
      </div>
    </div>
  );
};

export default PaymentPage;
